<template>
  <div>
    <h3>基本信息</h3>
    <div>
      <p>账户ID： {{this.$root.accountInfo.address}}</p>
      <p>账户PublicKey： {{this.$root.accountInfo.publicKey}}</p>
      <p>
        身份：
        <el-tag size="medium" :type="(!this.$root.accountInfo.isDelegate && !this.$root.accountInfo.isLC) ? 'success' : 'info'">普通用户</el-tag>
        <el-tag size="medium" :type="this.$root.accountInfo.isDelegate ? 'success' : 'info'">受托人</el-tag>
        <el-tag size="medium" :type="this.$root.accountInfo.isLC ? 'success' : 'info'">学习社区</el-tag>
      </p>
    </div>
    <div class="info-status-wrapper el-row">
      <div class="el-col el-col-6 el-col-xs-12 info-status">
        <div class="status-value">
          已注册社区
          <div class="status-value-right">{{ this.$root.accountInfo.auth ? this.$root.accountInfo.auth.length : 0}} 个</div>
        </div>
      </div>
      <div class="el-col el-col-6 el-col-xs-12 info-status">
        <div class="status-value">
          评价记录数
          <div class="status-value-right">{{ this.detailData.transactionCount==null ? '加载中...' : this.detailData.transactionCount + '条'}}</div>
        </div>
      </div>
      <div class="el-col el-col-6 el-col-xs-12 info-status">
        <div class="status-value">
          受托人得票率
          <div class="status-value-right">{{ this.$root.accountInfo.isDelegate ? ((this.$root.accountInfo.vote * 100 /  this.$root.allVotedCount).toFixed(2) + '%') : "--"}}</div>
        </div>
      </div>
      <div class="el-col el-col-6 el-col-xs-12 info-status">
        <div class="status-value">
          受托人投票数
          <div class="status-value-right">{{ this.$root.accountInfo.delegates ? this.$root.accountInfo.delegates.length : 0}}票</div>
        </div>
      </div>
    </div>
    <div>
      <h3>最近记录</h3>
      <el-table
        :data="tableData"
        stripe
        style="width: 100%">
        <el-table-column
          prop="id"
          label="记录ID"
          >
        </el-table-column>
        <el-table-column
          prop="blockId"
          label="区块ID"
          >
        </el-table-column>
        <el-table-column
          prop="height"
          label="区块高度">
        </el-table-column>
        <el-table-column
          label="指标变化">
          <template slot-scope="scope">
            <el-tag size="medium" :type="scope.row.amount >= 0 ? 'success' : 'danger'">{{ scope.row.amount }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="senderUsername"
          label="记录社区">
          <template slot-scope="scope">
            {{scope.row.senderUsername ? scope.row.senderUsername : '未知'}}
          </template>
        </el-table-column>
        <el-table-column
          prop="senderId"
          label="记录社区ID">
        </el-table-column>
        <el-table-column
          label="时间">
          <template slot-scope="scope">
            {{ scope.row.timestamp | dateFilter }}
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  export default {
    name: "account-info",
    data() {
      return {
        detailData:{
          transactionCount:null
        },
        tableData: []
      }
    },
    mounted() {
      this.$root.eventHub.$emit('flush', this.fetchData)
    },
    methods:{
      fetchData(){
        this.$http.get('http://localhost:7000/api/transactions',{params:{recipientId:this.$root.accountInfo.address}}).then(
          (res)=>{
            let filtered = this.$root.filterTransaction(res.body.transactions)
            this.detailData.transactionCount = filtered.length
            this.tableData = filtered.slice(-10).reverse()
          }
        )
      }
    }

  }
</script>

<style scoped>
  .info-status-wrapper {
    width: 100%;
    height: 130px;
  }

  .info-status {
    height: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }

  .status-value {
    line-height: 100%;
    padding: 20px;
    border: 1px solid #c9c9c9;
    border-top: 3px solid #0081ad;
    border-radius: 15px;
    box-shadow: rgba(128, 128, 128, 0.34) 0px 1px 20px 1px;
    font-size: xx-large;
    font-weight: bolder;
    color: #72abb6;

  }

  .status-value-right {
    width: 100%;
    text-align: right;
    color: darkgrey;
  }
</style>



