<template>
  <div>
    <div>
      <h3>受托人介绍</h3>
      <el-alert
        title="什么是受托人？"
        type="success"
        description="DPoS机制，中文名叫做股份授权证明机制（又称受托人机制），它的原理是让每一个用户进行投票，由此产生101位代表 , 我们可以将其理解为101个超级节点或者矿池，而这101个超级节点彼此的权利是完全相等的。从某种角度来看，DPOS有点像是议会制度或人民代表大会制度。如果代表不能履行他们的职责（当轮到他们时，没能生成区块），他们会被除名，网络会选出新的超级节点来取代他们。DPOS的出现最主要还是因为矿机的产生，大量的算力在不了解也不关心比特币的人身上，类似演唱会的黄牛，大量囤票而丝毫不关心演唱会的内容。">
      </el-alert>
    </div>
    <div>
      <h3>申请成为受托人</h3>
      <el-row style="text-align: center">
        <el-button type="primary" round @click="applyClicked" :disabled="applyed">
          <span v-if="applyed">已申请</span>
          <span v-else>我要申请</span>
        </el-button>
        <p style="color: grey;font-size: small">点击上方按钮即可申请</p>
      </el-row>
    </div>
    <div>
      <h3>申请进度</h3>
      <el-row>
        <p>得票率：{{(this.vote *100 / this.$root.allVotedCount).toFixed(2) + "%"}}</p>
        <p>第101名得票率：{{(this.vote101 *100 / this.$root.allVotedCount).toFixed(2) + "%"}}</p>
        <p>排名：  {{this.rate}}</p>
      </el-row>
      <el-row>
        <el-steps :active="steps" align-center finish-status="success">
          <el-step title="步骤1" description="提交受托人申请，成为候选受托人"></el-step>
          <el-step title="步骤2" description="收集选票"></el-step>
          <el-step title="步骤3" description="得票率在前101位，成为受托人"></el-step>
        </el-steps>
      </el-row>
    </div>

  </div>
</template>

<script>
  import ElRow from "element-ui/packages/row/src/row";

  export default {
    components: {ElRow},
    name: "apply-delegate",
    data() {
      return {
        applyed: false,
        steps: 0,
        username: '',
        rate:0,
        vote:0,
        vote101:0
      }
    },
    mounted(){
      this.$root.eventHub.$emit('flush', this.init)
    },
    methods: {
      init(){
        if(this.$root.accountInfo.isDelegate){
          this.applyed =true
          let index = this.$root.delegateList.indexOf(this.$root.accountInfo.address)

          if(index>=0){
            this.steps = 3
            this.rate = this.$root.delegateInfo[index].rate
            this.vote = this.$root.delegateInfo[index].vote
          }else{
            this.steps = 2
            this.rate = '>101'
            this.vote = this.$root.accountInfo.vote
          }
          this.vote101 = this.$root.delegateInfo[this.$root.delegateInfo.length-1].vote
        }
      },
      applyClicked: function () {
        this.$confirm('此操作不可撤销, 是否继续?', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if (this.$root.accountInfo.username === "") {
            this.$prompt('请输入受托人名称，请注意该名称将与您的帐号绑定，不可修改', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              inputValidator: (val) => {
                if (val.length < 2 || val.length > 20) {
                  return '名称长度在2-20个字符之间'
                } else {
                  return true
                }
              }
            }).then(({value}) => {
              this.username = value
              this.apply()
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '您取消了申请'
              });
            });
          }else {
            this.apply()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '您取消了申请'
          });
        });

      },
      apply: function () {
        this.$http.put("http://localhost:7000/api/delegates", {
          secret: this.$root.secret,
          username: this.username || this.$root.accountInfo.username
        }).then(
          (res) => {
            if (res.body.success) {
              this.$message.success({
                message: '申请成功!'
              });
              this.applyed = true
              this.steps = 1
            } else {
              this.$message.error({
                message: '申请失败' + res.body.error
              });
            }
          },
          (res)=>{
            this.$message.error('网络连接失败')
          }
          )
      }
    }
  }
</script>

<style scoped>

</style>



