<template>
  <div>
    <div>
      <h3>受托人投票</h3>
      <el-card class="box-card">
        <strong>投票须知</strong>
        <div class="text item">1.所用用户都可以为候选受托人投票</div>
        <div class="text item">2.每位用户对同一受托人至多只能投一票</div>
        <div class="text item">3.投票后可以撤销</div>
        <div class="text item">4.每经过一个受托人周期后（约16分钟）刷新得票率，得票率在前101位的候选受托人成为下一任受托人</div>
      </el-card>
    </div>
    <div>
      <el-table
        ref="multipleTable"
        :data="delegatesTableData"
        tooltip-effect="dark"
        style="width: 100%; margin-top: 10px"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          label="选择"
          :selectable="selectable"
          width="55">
        </el-table-column>
        <el-table-column
          label="排名"
          width="120"
          prop="rate">
        </el-table-column>
        <el-table-column
          label="受托人ID"
          prop="address">
        </el-table-column>
        <el-table-column
          label="受托人名称"
          prop="username">
        </el-table-column>
        <el-table-column
          label="区块产出率"
          width="120">
          <template slot-scope="scope">
            {{scope.row.productivity + "%"}}
          </template>
        </el-table-column>
        <el-table-column
          label="得票率"
          width="120">
          <template slot-scope="scope">
            {{(scope.row.vote *100 / $root.allVotedCount).toFixed(2) + "%"}}
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        small
        layout="prev, pager, next"
        :total="totalCount"
        @current-change="pageChange"
        style="width: 100%; margin-top: 10px; text-align: center"
      >

      </el-pagination>
      <div style="float: right">
        <el-button size="small" @click="toggleSelection()">取消选择</el-button>
        <el-button size="small" type="success" @click="voteClick()">确认投票</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "vote-delegate",
    data() {
      return {
        delegatesTableData: [],
        pageSize: 10,
        currentPage: 1,
        totalCount: 0,
        multipleSelection: []
      }
    },
    mounted() {
      this.$root.eventHub.$emit('flush',this.fetchData)
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      fetchData() {
        this.$http.get("http://localhost:7000/api/delegates", {
          params: {
            limit: this.pageSize,
            offset: (this.currentPage - 1) * this.pageSize,
            orderBy: 'rate:asc',
            random: Math.random() + "."
          }
        }).then(
          (res) => {
            if (res.body.success) {
              this.totalCount = res.body.totalCount
              this.delegatesTableData = res.body.delegates
            }
          })
      },
      pageChange(page) {
        this.currentPage = page
        this.fetchData()
      },
      selectable(row, index) {
        return this.$root.accountInfo.delegates.indexOf(row.publicKey) < 0
      },
      voteClick() {
        if(this.multipleSelection.length<=0){
          this.$message.error("请选择候选受托人")
return
        }
        this.$confirm('你确定为这' + this.multipleSelection.length + '个候选受托人投票吗？', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let list = []
          for (let i = 0; i < this.multipleSelection.length; i++) {
            list.push("+" + this.multipleSelection[i].publicKey)
          }
          this.$http.put('http://localhost:7000/api/accounts/delegates', {
            secret: this.$root.secret,
            delegates: list,
            publicKey: this.$root.accountInfo.publicKey
          }).then(
            (res) => {
              if (res.body.success) {
                this.$message.success("投票成功")
              } else {
                this.$message.error("投票失败" + res.body.error)
              }
            },
            (res) => {
              this.$message.error("网络连接失败")
            }
          )
        })
      }
    }
  }
</script>

<style scoped>

</style>



