<template>
  <div>
    <el-container>
      <el-header>
        <h2>基于区块链的学习社区用户评价系统</h2>
        <h4>-后台登录-</h4>
      </el-header>
      <el-main>
        <el-form :model="loginForm" :rules="rules" status-icon ref="loginForm" label-width="80px">
          <el-form-item label="登录口令" prop="pass">
            <el-input type="password" v-model="loginForm.pass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-checkbox v-model="loginForm.rememberChecked">记住密码</el-checkbox>
          </el-form-item>
          <el-form-item style="float: right">
            <el-button @click="dialogFormVisible = true">注册</el-button>
            <el-button type="primary" @click="submitForm('loginForm')">登录</el-button>
          </el-form-item>
        </el-form>


      </el-main>
      <el-footer>
        <p>毕业设计作品 2018年4月 By 周于涛</p>
      </el-footer>
    </el-container>
    <el-dialog title="注册新用户" :visible.sync="dialogFormVisible">
      <el-form :model="rigiForm" :rules="rules" status-icon ref="rigiForm" label-width="100px">
        <el-form-item label="登录口令" prop="pass1">
          <el-input type="password" v-model="rigiForm.pass1" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认登录口令" prop="pass2">
          <el-input type="password" v-model="rigiForm.pass2" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="rigiComplete('rigiForm')">注 册</el-button>
        </el-form-item>
      </el-form>

    </el-dialog>
  </div>


</template>
<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        }
        callback();
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else if (value !== this.rigiForm.pass1) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        loginForm: {
          pass: '',
          rememberChecked: false,
        },
        rigiForm: {
          pass1: '',
          pass2: ''
        },
        dialogFormVisible: false,
        rules: {
          pass: [
            {validator: validatePass, trigger: 'blur'}
          ],
          pass1: [
            {validator: validatePass, trigger: 'blur'}
          ],
          pass2: [
            {validator: validatePass2, trigger: 'blur'}
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.post('http://localhost:7000/api/accounts/open', {secret: this.loginForm.pass}).then(
              (res) => {
                this.$root.secret = this.loginForm.pass
                this.$root.address = res.body.account.address
                this.$root.eventHub.$emit('flush', () =>{
                  this.$router.push('/account/info')
                })
              },
              (res) => {
                this.$message.error('服务器连接失败，请检查网络');
              })
          } else {
            return false;
          }
        });
      },
      rigiComplete(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.dialogFormVisible = false
          } else {
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-main {
    width: 30%;
    top: 50%;
    left: 50%;
    transform: translateY(-60%)translateX(-50%);
    position: absolute;
    margin: 0 auto;
    padding: 30px;
    border: 1px solid #d1d1d1;
    border-radius: 20px;
    box-shadow: rgba(128, 128, 128, 0.34) 0px 1px 25px 1px;
  }

  .el-header {
    text-align: center;
    height: 100px !important;
  }
  .el-footer{
    position: fixed;
    bottom: 0;
    text-align: center;
    width: 100%;
  }
</style>



