import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Dashboard from '@/components/Dashboard'

import AccountInfo from '@/components/subComp/AccountInfo'
import AccountRecord from '@/components/subComp/AccountRecord'
import AccountAuth from '@/components/subComp/AccountAuth'
import VoteDelegate from '@/components/subComp/VoteDelegate'
import VoteLC from '@/components/subComp/VoteLC'
import ApplyDelegate from '@/components/subComp/ApplyDelegate'
import ApplyLC from '@/components/subComp/ApplyLC'
import OverviewDelegate from '@/components/subComp/OverviewDelegate'
import OverviewLC from '@/components/subComp/OverviewLC'
import OverviewBlockchain from '@/components/subComp/OverviewBlockchain'
import ApiPubilc from '@/components/subComp/ApiPublic'
import ApiPrivate from '@/components/subComp/ApiPrivate'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
      children:[
        {
          path:'/account/info',
          component: AccountInfo
        },
        {
          path:'/account/record',
          component: AccountRecord
        },
        {
          path:'/account/auth',
          component: AccountAuth
        },
        {
          path:'/vote/delegate',
          component: VoteDelegate
        },
        {
          path:'/vote/lc',
          component: VoteLC
        },
        {
          path:'/apply/delegate',
          component: ApplyDelegate
        },
        {
          path:'/apply/lc',
          component: ApplyLC
        },
        {
          path:'/overview/delegate',
          component: OverviewDelegate
        },
        {
          path:'/overview/lc',
          component: OverviewLC
        },
        {
          path:'/overview/blockchain',
          component: OverviewBlockchain
        },
        {
          path:'/api/public',
          component: ApiPubilc
        },
        {
          path:'/api/lc',
          component: ApiPrivate
        }
      ]
    }
  ]
})



