<template>
  <div>
    <div>
      <h3>学习社区信息</h3>
      <div>
        <h4>查询条件</h4>
        <span>学习社区名称：</span>
        <el-select v-model="selectedLC" placeholder="请选择" @change="selectChange">
          <el-option
            v-for="item in this.$root.lcInfo"
            :key="item.address"
            :label="item.username"
            :value="item.address"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <div>
      <el-table
        ref="multipleTable"
        :data="lcsTableData"
        tooltip-effect="dark"
        style="width: 100%; margin-top: 10px">
        <el-table-column
          type="index"
          width="120"
          prop="rate">
        </el-table-column>
        <el-table-column
          label="学习社区ID"
          prop="address">
        </el-table-column>
        <el-table-column
          label="学习社区名称"
          prop="username">
        </el-table-column>
        <el-table-column
          label="得票数"
          width="120">
          <template slot-scope="scope">
            {{scope.row.lc_vote}}
          </template>
        </el-table-column>
        <el-table-column
          label="得票率"
          width="120">
          <template slot-scope="scope">
            {{(scope.row.lc_vote *100 / 101).toFixed(2) + "%"}}
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        small
        layout="prev, pager, next"
        :total="totalCount"
        @current-change="pageChange"
        style="width: 100%; margin-top: 10px; text-align: center"
      >

      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: "overview-l-c",
    data(){
      return{
        lcsTableData: [],
        pageSize: 10,
        currentPage: 1,
        totalCount: 0,
        selectedLC:''
      }
    },
    mounted(){
      this.fetchData()
    },
    methods:{
      fetchData() {
        this.totalCount = this.$root.lcInfo.length
        this.lcsTableData = this.$root.lcInfo.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
      },
      pageChange(page) {
        this.currentPage = page
        this.fetchData()
      },
      selectChange(val){
        this.lcsTableData = [this.$root.lcInfo[this.$root.lcList.indexOf(val)]]
      }
    }
  }
</script>

<style scoped>

</style>




