<template>
  <el-container>

    <el-container>
      <el-aside width="300px" style="background-color: rgb(238, 241, 246); position: fixed" >
        <el-menu :default-openeds="['1','2','3','4']"
                 background-color="#545c64"
                 text-color="#fff"
                 active-text-color="#ffd04b"
        >
          <el-submenu index="1">
            <template slot="title" ><i class="el-icon-message" ></i>帐号信息</template>
            <el-menu-item-group>
              <el-menu-item index="1-1" @click="goRouter('/account/info')">账户状态</el-menu-item>
              <el-menu-item index="1-2" @click="goRouter('/account/record')">评价记录</el-menu-item>
              <el-menu-item index="1-3" @click="goRouter('/account/auth')">账户授权</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"  ><i class="el-icon-menu"></i>申请与投票</template>
            <el-menu-item-group>
              <template slot="title" >申请</template>
              <el-menu-item index="2-1" @click="goRouter('/apply/delegate')">受托人</el-menu-item>
              <el-menu-item index="2-2" @click="goRouter('/apply/lc')">学习社区</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="投票">
              <el-menu-item index="2-3" @click="goRouter('/vote/delegate')">受托人</el-menu-item>
              <el-menu-item index="2-4" @click="goRouter('/vote/lc')">学习社区</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"  ><i class="el-icon-setting"></i>信息查询</template>
            <el-menu-item-group>
              <el-menu-item index="3-1" @click="goRouter('/overview/delegate')">受托人</el-menu-item>
              <el-menu-item index="3-2" @click="goRouter('/overview/lc')">学习社区</el-menu-item>
              <el-menu-item index="3-3" @click="goRouter('/overview/blockchain')">区块链</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="4">
            <template slot="title"  ><i class="el-icon-share"></i>API</template>
            <el-menu-item-group>
              <el-menu-item index="4-1" @click="goRouter('/api/public')">公用API</el-menu-item>
              <el-menu-item index="4-2" @click="goRouter('/api/lc')">受托人/学习社区 API</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-container style="margin-left: 300px;">
        <el-header style="background: #545c64; font-size: x-large; font-weight: bolder; color: #e6f2ef; border-bottom: 1px solid rgba(166,185,186,0.79)">
          <p style="margin: 0; line-height: 62px">
            基于区块链的学习社区用户评价系统
            <a style="float: right; font-size: medium; color: whitesmoke" href="#" @click="loginout"><i class="el-icon-back"></i>退出登录</a>
          </p>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>

  </el-container>

</template>

<script>
  export default {
    name: "dashboard",
    data(){
      const item = {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      };
      return {
        tableData: Array(20).fill(item)
      }
    },
    methods:{
      goRouter(router){
        this.$router.push(router)
      },
      loginout(){
        this.$router.push('/')
      }
    }
  }
</script>

<style scoped>
  .el-aside, .el-main{
    overflow: visible;
    width: 100%;
  }

  .el-menu{
    border-right: 1px solid #545c64;
  }
  .el-aside{
    box-shadow: 0px 1px 10px black;
  }
</style>
<style>
</style>



