<template>
  <div>
    <div id="section-left">
      <h3>公用API（点击可查看详情）</h3>
      <el-collapse v-model="activeName" accordion>
        <el-collapse-item v-for="item in items" :key="item.name" :name="item.name" style="font-weight: bolder">
          <template slot="title" >
            <span  style="font-size: large; padding-left: 20px">{{item.name+' '+item.method+' '+item.url}}</span>
          </template>

          <div class="item-div">方法：{{item.method}}</div>
          <div class="item-div">URL：{{item.url}}</div>
          <div class="item-div">描述：{{item.desp}}</div>
          <div class="item-div">
            参数
            <ul>
              <li v-for="param in item.params" :key="param.key" style="margin-top: 10px">
                名称：<el-tag size="small" type="success">{{param.key}}</el-tag>
                类型：<el-tag size="small" type="success">{{param.type}}</el-tag>
                说明：<el-tag size="small" type="success">{{param.desp}}</el-tag>
                是否必须：<el-tag size="small" type="success" v-if="param.must">是</el-tag><el-tag size="small" type="success" v-else>否</el-tag>
              </li>
            </ul>
          </div>
          <div class="item-div">
            <el-button size="mini" type="warning" @click="useApi(item)">使用</el-button>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
    <div id="section-right">
      <div>
        <h3>API使用</h3>
        <el-form :inline="true" class="form-inline">
          <el-form-item label="URL">
            <el-input v-model="apiForm.url" placeholder="URL"></el-input>
          </el-form-item>
          <el-form-item label="方法">
            <el-select v-model="apiForm.method" placeholder="方法">
              <el-option label="GET" value="GET"></el-option>
              <el-option label="POST" value="POST"></el-option>
              <el-option label="PUT" value="PUT"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="apiSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <el-form label-position="left">
          <el-form-item v-for="item in paramsForm" :key="item.key" :label="item.placeholder" size="mini">
            <el-input :type="item.type" v-model="item.value" :placeholder="item.placeholder"></el-input>
          </el-form-item>
          <el-form-item label="返回结果" prop="desc">
            <el-input type="textarea" v-model="apiForm.result" rows="6"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "api-public",
    data(){
      return{
        activeName:'',
        apiForm: {
          url: '',
          method: '',
          result: ''
        },
        paramsForm: [
          // { key: "secret",
          //   type:'password',
          //   value:"1",
          //   placeholder:"口令"
          // },
          // {key: "publicKey",
          //   type:'text',
          //   value:"fdsfdsfdfdsfsdfsdfsd",
          //   placeholder:"公钥"
          // }
        ],
        items:[
          {
            method:'POST',
            url: ' /api/accounts/open',
            name:'注册用户',
            desp:'注册成为平台用户，注册成功后是普通用户的身份',
            params:[
              {
                key:'secret',
                type:'String',
                desp:'登录口令',
                must:true
              }
            ]
          },
          {
            method:'PUT',
            url: '/api/delegates/',
            desp:'申请成为受托人',
            name:'申请成为受托人',
            params:[
              {
                key:'username',
                type:'String',
                desp:'受托人名称',
                must:true
              },
              {
                key:'secret',
                type:'String',
                desp:'登录口令',
                must:true
              }
            ]
          },
          {
            method:'PUT',
            url: '/api/lcs/',
            name:'申请成为学习社区',
            desp:'申请成为学习社区',
            params:[
              {
                key:'username',
                type:'String',
                desp:'学习社区名称',
                must:true
              },
              {
                key:'secret',
                type:'String',
                desp:'登录口令',
                must:true
              }
            ]
          },
          {
            method:'GET',
            url: '/api/delegates',
            name:'查询受托人',
            desp:'查询受托人',
            params:[
              {
                key:'username',
                type:'String',
                desp:'受托人名称'
              },
              {
                key:'address',
                type:'String',
                desp:'受托人ID'
              },
            ]
          },
          {
            method:'GET',
            url: '/api/lcs',
            name:'查询学习社区',
            desp:'查询学习社区',
            params:[
              {
                key:'username',
                type:'String',
                desp:'学习社区名称'
              },
              {
                key:'address',
                type:'String',
                desp:'学习社区ID'
              },
            ]
          },
          {
            method:'GET',
            url: '/api/transactions',
            name:'查询区块链上的记录',
            desp:'查询区块链上的记录',
            params:[
              {
                key:'type',
                type:'Number',
                desp:'记录类型'
              },
              {
                key:'senderId',
                type:'String',
                desp:'发起账号'
              },
              {
                key:'recipientId',
                type:'String',
                desp:'目标账号'
              },
            ]
          },
          {
            method:'PUT',
            url: '/api/accounts/delegates',
            desp:'用户为受托人投票',
            name:'为受托人投票',
            params:[
              {
                key:'secret',
                type:'String',
                desp:'登录口令',
                must:true
              },
              {
                key:'publicKey',
                type:'String',
                desp:'用户公钥',
                must:true
              },
              {
                key:'delegates',
                type:'Array',
                desp:'投票目标的PublicKey组成的列表，PublicKey前需要加一个“+”（投票）或者“-”号（撤销投票）',
                must:true
              }
            ]
          },
          {
            method:'PUT',
            url: '/api/accounts/auth',
            desp:'用户向学习社区授权，接受学习社区的评价',
            name:'向学习社区授权',
            params:[
              {
                key:'secret',
                type:'String',
                desp:'登录口令',
                must:true
              },
              {
                key:'publicKey',
                type:'String',
                desp:'用户公钥',
                must:true
              },
              {
                key:'auth',
                type:'Array',
                desp:'投票目标的PublicKey组成的列表，PublicKey前需要加一个“+”（授权）或者“-”号（取消授权）',
                must:true
              }
            ]
          }
        ]
      }
    },
    methods:{
      useApi(item) {
        this.apiForm.method = item.method
        this.apiForm.url = item.url
        this.paramsForm = []
        for (let i = 0; i < item.params.length; i++) {
          this.paramsForm.push(
            {
              key: item.params[i].key,
              type: item.params[i].key === 'secret' ? 'password' : 'text',
              value: item.params[i].key === 'secret' ? this.$root.secret : (item.params[i].key === 'publicKey' ? this.$root.accountInfo.publicKey : ''),
              valueType: item.params[i].type,
              placeholder: item.params[i].type === 'Array' ? item.params[i].desp + "(多个值用“,”隔开)" : item.params[i].desp
            })
        }
      },
      apiSubmit() {
        this.$http[this.apiForm.method.toLowerCase()]("http://localhost:7000" + this.apiForm.url.trim(), this.getParams(this.apiForm.method)).then(
          (res) => {
            this.apiForm.result = JSON.stringify(res.body)
          },
          (res) => {
            this.$message.error("网络连接失败")
          }
        )
      },
      getParams(method) {
        let params = {}
        for (let i = 0; i < this.paramsForm.length; i++) {
          if(this.paramsForm[i].value === ''){
            continue
          }
          if (this.paramsForm[i].valueType === 'Array') {
            params[this.paramsForm[i].key] = this.paramsForm[i].value.split(',')
          } else if (this.paramsForm[i].valueType === 'Number') {
            params[this.paramsForm[i].key] = parseInt(this.paramsForm[i].value)
          } else {
            params[this.paramsForm[i].key] = this.paramsForm[i].value
          }
        }
        return method.toLowerCase() === 'get' ? {params: params} : params
      }
    }
  }
</script>

<style scoped>
  .item-div{
  padding: 6px 20px;
}
  #section-left {
    width: 58%;
    display: inline-block;
  }

  #section-right {
    width: 30%;
    display: inline-block;
    vertical-align: top;
    margin-left: 3%;
    padding-left: 3%;
    border-left: 1px solid #c9c9c9;
    min-height: 700px;
  }
</style>



