// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App'

import router from './router'
import {filter} from './assets/js/filter'

Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.use(VueResource)
Vue.filter('dateFilter', filter.dateFilter)
Vue.filter('typeFilter', filter.typeFilter)

Vue.prototype.secret = '';
Vue.prototype.accountInfo = {};
Vue.prototype.lcInfo = [];
Vue.prototype.delegateInfo = [];
Vue.prototype.lcList = [];
Vue.prototype.delegateList = [];
Vue.prototype.allVotedCount = 1;

Vue.prototype.filterTransaction = function (transactions) {
  let res = []
  for (let i = 0; i < transactions.length; i++) {
    // 查询是否为学习社区,type是否为0
    for(let j=0;j<this.$root.lcInfo.length; j++){
      if(transactions[i].senderId === this.$root.lcInfo[j].address && transactions[i].type === 0){
        transactions[i].senderUsername = this.$root.lcInfo[j].username
        res.push(transactions[i])
        break
      }
    }
  }
  return res
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  data:{
    eventHub:new Vue()
  },
  components: { App },
  template: '<App/>'
})



