<template>
  <div>
    <h3>授权</h3>
    <el-table
      :data="authTableData"
      style="width: 100%">
      <el-table-column
        label="学习社区ID"
        prop="address"
      >
      </el-table-column>
      <el-table-column
        prop="username"
        label="学习社区名">
      </el-table-column>
      <el-table-column
        prop="lc_vote"
        label="得票数">
      </el-table-column>
      <el-table-column
        label="得票率">
        <template slot-scope="scope">
          {{(scope.row.lc_vote / 101 * 100).toFixed(2) + "%"}}
        </template>
      </el-table-column>

      <el-table-column label="是否授权" prop="isAuthed">
        <template slot-scope="scope">
          <span v-show="scope.row.isAuthed === true" style="color: green">已授权</span>
          <span v-show="scope.row.isAuthed === false" style="color: orange">未授权</span>
          <span v-show="scope.row.isAuthed === undefined" style="color: grey">信息获取中...</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="small"
            type="danger"
            @click="handleUnAuth(scope.$index, scope.row)"
            v-show="scope.row.isAuthed === true"
          >取消授权</el-button>
          <el-button
            size="small"
            @click="handleAuth(scope.$index, scope.row)"
            v-show="scope.row.isAuthed === false"
          >授权</el-button>
          <el-button
            size="small"
            @click="handleAuth(scope.$index, scope.row)"
            v-show="scope.row.isAuthed === undefined"
            disabled
          >等待确认中</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: "account-auth",
    data(){
      return{
        authTableData:[]
      }
    },
    methods:{
      fetchData(){
        let res = []
        let authed = this.$root.accountInfo.auth
        for(let i =0;i< this.$root.lcInfo.length;i++){
          res.push(this.$root.lcInfo[i])
          if(authed.indexOf(res[i].publicKey) >=0 ){
            res[i].isAuthed = true
          }else {
            res[i].isAuthed = false
          }
        }
        this.authTableData = res
      },
      handleAuth(index, row){
        this.$http.put("http://localhost:7000/api/accounts/auth",
          {"secret":this.$root.secret,
            "auth":["+"+row.publicKey],
            "publicKey":this.$root.accountInfo.publicKey}).then(
          (res)=>{
            if(res.body.success){
              this.authTableData[index].isAuthed = undefined
            }else {
              this.$message.error('授权失败 \n'+res.body.error)
            }
            setTimeout( ()=>{
              this.$message.success('授权成功')
              this.$root.eventHub.$emit('flush', this.fetchData)
            },10000)
          },
          (res)=>{
            this.$message.error('网络连接失败')
          }
        )
      },
      handleUnAuth(index, row){
        this.$http.put("http://localhost:7000/api/accounts/auth",
          {"secret":this.$root.secret,
            "auth":["-"+row.publicKey],
            "publicKey":this.$root.accountInfo.publicKey}).then(
          (res)=>{
            if(res.body.success){
              this.authTableData[index].isAuthed = undefined
            }else {
              this.$message.error('取消授权失败 \n'+res.body.error)
            }
            setTimeout( ()=> {
              this.$root.eventHub.$emit('flush', this.fetchData)
              this.$message.success('取消授权成功')
            },10000)
          },
          (res)=>{
            this.$message.error('网络连接失败')
          }
        )
      }
    },
    mounted(){
      this.$root.eventHub.$emit('flush',this.fetchData)
    }

  }
</script>

<style scoped>

</style>



