import moment from 'moment'

export let filter =  {
  dateFilter(timestamp) {
    return moment.unix(1466380800+timestamp).format('YYYY-MM-DD hh:mm:ss')
  },
  typeFilter(type) {
    let obj = {
      '0':'社区记录',
      '2':'受托人申请',
      '3':'受托人投票',
      '4':'用户名注册',
      '14':'学习社区申请',
      '15':'学习社区投票',
      '16':'授权'
    }
    return obj[type] || '未知类型'
  }
}



