<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      s:null
    }
  },
  created(){
    this.$root.eventHub.$on('loginSuccess',  ()=> {
     this.s = setInterval(()=> {
        this.$http.get("http://localhost:7000/api/accounts", {params: {address: this.$root.address, random:Math.random()+"."}}).then((res) => {
          this.$root.accountInfo = res.body.account || {}
        })
      }, 10000)
    })

    this.$root.eventHub.$on('flush',  (cb)=> {
      // 请求用户数据
      this.$http.get("http://localhost:7000/api/accounts", {params: {address: this.$root.address, random:Math.random()+"."}}).then((res) => {
        this.$root.accountInfo = res.body.account || {}
        // 请求学习社区数据
        this.$http.get("http://localhost:7000/api/lcs", {
          params: {
            random: Math.random() + "."
          }
        }).then(
          (res) => {
            let list = []
            let list2 = []
            for(let i =0;i<res.body.lcs.length;i++){
              if (res.body.lcs[i].lc_vote >= 1){
                list.push(res.body.lcs[i].address)
                list2.push(res.body.lcs[i])
              }
            }
            this.$root.lcList = list
            this.$root.lcInfo = list2
          })
        // 请求受托人数据(前101)
        this.$http.get("http://localhost:7000/api/delegates", {
          params: {
            limit:101,
            offset:0,
            orderBy:'rate:asc',
            random: Math.random() + "."
          }
        }).then(
          (res) => {
            let list = []
            let list2 = []
            for(let i =0;i<res.body.delegates.length;i++){
              list.push(res.body.delegates[i].address)
              list2.push(res.body.delegates[i])
            }
            this.$root.delegateList = list
            this.$root.delegateInfo = list2
          })
        // 请求全部帐号数据,计算已为受托人投票的用户数
        this.$http.get("http://localhost:7000/api/accounts/all", {
          params: {
            random: Math.random() + "."
          }
        }).then(
          (res) => {
            if(res.body.success){
              let cnt = 0
              for(let i=0;i<res.body.account.length;i++){
                if(res.body.account[i].delegates.length > 0){
                  cnt++
                }
              }
              this.$root.allVotedCount = cnt
            }
          })

        cb()
      })
    })
  },
  mounted(){

  },
  destroyed(){
    clearInterval(this.s)
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
  body{
    margin: 0;
  }
  h3{
    margin-top: 0.4em !important;
    margin-bottom: 0.4em !important;
    background: aliceblue;
    height: 2em;
    padding-left: 0.5em;
    line-height: 2em;
  }
</style>



