<template>
  <div>
    <div>
      <h3>受托人信息</h3>
      <div>
        <h4>查询条件</h4>
        <span>受托人名称：</span>
        <el-select v-model="selectedDelegate" placeholder="请选择" @change="selectChange">
          <el-option
            v-for="item in this.$root.delegateInfo"
            :key="item.address"
            :label="item.username"
            :value="item.address"
            >
          </el-option>
        </el-select>
      </div>
    </div>
<div>
  <el-table
    ref="multipleTable"
    :data="delegatesTableData"
    tooltip-effect="dark"
    style="width: 100%; margin-top: 10px">
    <el-table-column
      label="排名"
      width="120"
      prop="rate">
    </el-table-column>
    <el-table-column
      label="受托人ID"
      prop="address">
    </el-table-column>
    <el-table-column
      label="受托人名称"
      prop="username">
    </el-table-column>
    <el-table-column
      label="区块产出率"
      width="120">
      <template slot-scope="scope">
        {{scope.row.productivity + "%"}}
      </template>
    </el-table-column>
    <el-table-column
      label="得票率"
      width="120">
      <template slot-scope="scope">
        {{(scope.row.vote *100 / $root.allVotedCount).toFixed(2) + "%"}}
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    background
    small
    layout="prev, pager, next"
    :total="totalCount"
    @current-change="pageChange"
    style="width: 100%; margin-top: 10px; text-align: center"
  >

  </el-pagination>
</div>
  </div>
</template>

<script>
  export default {
    name: "overview-delegate",
    data(){
      return{
        delegatesTableData: [],
        pageSize: 10,
        currentPage: 1,
        totalCount: 0,
        selectedDelegate:'',
      }
    },
    mounted(){
      this.fetchData()
    },
    methods:{
      fetchData() {
        this.totalCount = this.$root.delegateInfo.length
        this.delegatesTableData = this.$root.delegateInfo.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
      },
      pageChange(page) {
        this.currentPage = page
        this.fetchData()
      },
      selectChange(val){
        this.delegatesTableData = [this.$root.delegateInfo[this.$root.delegateList.indexOf(val)]]
      }
    }
  }
</script>

<style scoped>

</style>



