<template>
  <div>
    <h3>区块链浏览</h3>
    <el-table
      ref="multipleTable"
      :data="blocksTableData"
      tooltip-effect="dark"
      style="width: 100%; margin-top: 10px">
      <el-table-column
        label="区块高度"
        width="120"
        prop="height"
        align="center">
      </el-table-column>
      <el-table-column
        label="区块ID"
        align="center"
        prop="id">
      </el-table-column>
      <el-table-column
        label="区块地址"
        align="center"
        prop="generatorId">
      </el-table-column>
      <el-table-column
        label="日期"
        align="center">
        <template slot-scope="scope">
          {{scope.row.timestamp | dateFilter }}
        </template>
      </el-table-column>
      <el-table-column
        label="记录信息数量"
        align="center"
        width="120">
        <template slot-scope="scope">
          {{scope.row.numberOfTransactions}}
        </template>
      </el-table-column>
      <el-table-column
        label="记录信息"
        align="center"
        width="120">
        <template slot-scope="scope">
          <el-button type="success" size="small" @click="showDetail(scope.row.id)">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      small
      layout="prev, pager, next"
      :total="totalCount"
      @current-change="pageChange"
      style="width: 100%; margin-top: 10px; text-align: center"
    >

    </el-pagination>

    <el-dialog title="区块详情" :visible.sync="dialogTableVisible" width="70%">
      <el-table :data="transactionTableData">
        <el-table-column property="id" label="记录ID" ></el-table-column>
        <el-table-column property="confirmations" label="确认数"></el-table-column>
        <el-table-column
          label="时间">
          <template slot-scope="scope">
            {{ scope.row.timestamp | dateFilter }}
          </template>
        </el-table-column>
        <el-table-column property="senderId" label="发起账号"></el-table-column>
        <el-table-column property="recipientId" label="目标账号"></el-table-column>
        <el-table-column label="类型">
          <template slot-scope="scope">
            {{scope.row.type | typeFilter}}
          </template>
        </el-table-column>
        <el-table-column label="记录值变化">
          <template slot-scope="scope" >
            <el-tag v-if="scope.row.type === 0" size="medium" :type="scope.row.amount >= 0 ? 'success' : 'danger'">{{ scope.row.amount }}</el-tag>
            <span v-else>--</span>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "overview-blockchain",
    data(){
      return{
        blocksTableData:[],
        dialogTableVisible:false,
        transactionTableData:[],
        pageSize: 10,
        currentPage: 1,
        totalCount: 0,
        interval: null
      }
    },
    methods:{
      fetchData(){
        this.$http.get("http://localhost:7000/api/blocks",
          {
            params: {
              limit: this.pageSize,
              offset: (this.currentPage - 1) * this.pageSize,
              orderBy: 'height:desc',
              random: Math.random() + "."
            }
          }
        ).then((res)=>{
          if (res.body.success) {
            this.totalCount = res.body.count
            this.blocksTableData = res.body.blocks
          }
        })
      },
      pageChange(page) {
        this.currentPage = page
        this.fetchData()
      },
      showDetail(id){
        this.dialogTableVisible = true
        this.$http.get("http://localhost:7000/api/transactions",
          {
            params: {
              blockId:id,
              random: Math.random() + "."
            }
          }).then((res)=>{
            if(res.body.success){
              this.transactionTableData = res.body.transactions
            }else {
              this.$message.error("加载失败")
            }
        },(res)=>{
          this.$message.error("网络连接失败")
        })
      }
    },
    mounted(){
      this.fetchData()
      this.interval = setInterval(this.fetchData, 10000)
    },
    destroyed(){
      clearInterval(this.interval)
    }
  }
</script>

<style scoped>

</style>



