<template>
  <div>
    <div>
      <h3>各社区综合记录</h3>
      <el-table
        :data="partionTableData"
        style="width: 100%">
        <el-table-column
          prop="senderId"
          label="学习社区ID"
        >
        </el-table-column>
        <el-table-column
          prop="senderUsername"
          label="学习社区名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="totalAmount"
          label="综合评价值">
          <template slot-scope="scope">
            <el-tag size="medium" :type="scope.row.totalAmount >= 0 ? 'success' : 'danger'">{{ scope.row.totalAmount
              }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label="最近评价时间">
          <template slot-scope="scope">
            {{ scope.row.timestamp | dateFilter }}
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div>
      <div style="margin-bottom: 20px">
        <h3>
          全部记录
        </h3>
        筛选：
        <el-select value="" placeholder="请选择" @change="selectChange">
          <el-option
            v-for="item in this.$root.lcInfo"
            :key="item.address"
            :label="item.username"
            :value="item.address"
          >
          </el-option>
        </el-select>
      </div>

      <el-table
        :data="selectedRecord"
        style="width: 100%">
        <el-table-column
          prop="senderId"
          label="学习社区ID"
        >
        </el-table-column>
        <el-table-column
          prop="senderUsername"
          label="学习社区名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="amount"
          label="评价值">
          <template slot-scope="scope">
            <el-tag size="medium" :type="scope.row.amount >= 0 ? 'success' : 'danger'">{{ scope.row.amount }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label="评价时间">
          <template slot-scope="scope">
            {{ scope.row.timestamp | dateFilter }}
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        small
        layout="prev, pager, next"
        :total="totalCount"
        @current-change="pageChange"
        style="width: 100%; margin-top: 10px; text-align: center"
      >

      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: "account-record",
    data() {
      return {
        allRecord: [],
        // 筛选

        selectedLC: '',
        // key为学习社区ID，用于统计各学习社区的评价总和
        partionRecord: {},

        partionTableData: [],
        pageSize: 10,
        currentPage: 1,
        totalCount: 0,
      }
    },
    mounted() {
      this.fetchData()
      this.fetchPartionData()
    },
    methods: {
      selectChange(value) {
        this.selectedLC = value
      },
      pageChange(page) {
        this.currentPage = page
        this.fetchData()
      },
      fetchPartionData(){
        this.$http.get('http://localhost:7000/api/transactions', {
          params: {
            recipientId: this.$root.accountInfo.address,
          }
        }).then(
          (res) => {
            if(res.body.success){
              this.partionRecord = {}
              this.partionTableData = []

              for (let i = 0; i < res.body.transactions.length; i++) {
                // 地址第一次出现
                if (!this.partionRecord[res.body.transactions[i].senderId]) {
                  // 查询是否为学习社区
                  for (let j = 0; j < this.$root.lcInfo.length; j++) {
                    if (res.body.transactions[i].senderId === this.$root.lcInfo[j].address) {
                      this.partionRecord[res.body.transactions[i].senderId] = {
                        senderId: res.body.transactions[i].senderId,
                        senderUsername: this.$root.lcInfo[j].username,
                        totalAmount: res.body.transactions[i].amount,
                        timestamp: res.body.transactions[i].timestamp
                      }
                      break
                    }
                  }
                } else {
                  this.partionRecord[res.body.transactions[i].senderId].totalAmount += res.body.transactions[i].amount
                  this.partionRecord[res.body.transactions[i].senderId].timestamp = res.body.transactions[i].timestamp
                }
              }

              for (let i in this.partionRecord) {
                if (this.partionRecord.hasOwnProperty(i)) {
                  this.partionTableData.push(this.partionRecord[i])
                }
              }
            }else {
              this.$message.error('记录获取失败'+res.body.error)
            }

          }
        )
      },
      fetchData(){
        this.$http.get('http://localhost:7000/api/transactions', {
          params: {
            recipientId: this.$root.accountInfo.address,
            // limit: this.pageSize,
            // offset: (this.currentPage - 1) * this.pageSize,
          }
        }).then(
          (res) => {
            if(res.body.success){
              this.totalCount = res.body.count
              let filtered = this.$root.filterTransaction(res.body.transactions)
              this.allRecord = filtered.reverse()
              // this.selectedRecord = filtered
            }else {
              this.$message.error('记录获取失败'+res.body.error)
            }

          }
        )
      }
    },
    computed:{
      // 当前显示的记录，根据全记录（allRecord）以及筛选值（selectedLC）以及页码（currentPage）实时计算得到
      selectedRecord:function(){
        let list = []
        for (let i = 0; i < this.allRecord.length; i++) {
          if (this.selectedLC === '' || this.allRecord[i].senderId === this.selectedLC) {
            list.push(this.allRecord[i])
          }
        }
        this.totalCount = list.length
        return list.slice((this.currentPage - 1) * this.pageSize , (this.currentPage) * this.pageSize)
      }
    }
  }
</script>

<style scoped>

</style>



