<template>
  <div>
    <el-alert
      title="提示"
      type="warning"
      description="本页所展示的API仅限特定帐号使用">
    </el-alert>
    <div id="section-left">
      <div>
        <h3>受托人API（点击可查看详情）</h3>
        <el-collapse v-model="activeName" accordion>
          <el-collapse-item v-for="item in items1" :key="item.name" :name="item.name" style="font-weight: bolder">
            <template slot="title">
              <span style="font-size: large; padding-left: 20px">{{item.name+' '+item.method+' '+item.url}}</span>
            </template>

            <div class="item-div">方法：{{item.method}}</div>
            <div class="item-div">URL：{{item.url}}</div>
            <div class="item-div">描述：{{item.desp}}</div>
            <div class="item-div">
              参数
              <ul>
                <li v-for="param in item.params" :key="param.key" style="margin-top: 10px">
                  名称：
                  <el-tag size="small" type="success">{{param.key}}</el-tag>
                  类型：
                  <el-tag size="small" type="success">{{param.type}}</el-tag>
                  说明：
                  <el-tag size="small" type="success">{{param.desp}}</el-tag>
                  是否必须：
                  <el-tag size="small" type="success" v-if="param.must">是</el-tag>
                  <el-tag size="small" type="success" v-else>否</el-tag>
                </li>
              </ul>
            </div>
            <div class="item-div">
              <el-button size="mini" type="warning" @click="useApi(item)">使用</el-button>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <div>
        <h3>学习社区API（点击可查看详情）</h3>

        <el-collapse v-model="activeName" accordion>
          <el-collapse-item v-for="item in items2" :key="item.name" :name="item.name" style="font-weight: bolder">
            <template slot="title">
              <span style="font-size: large; padding-left: 20px">{{item.name+' '+item.method+' '+item.url}}</span>
            </template>

            <div class="item-div">方法：{{item.method}}</div>
            <div class="item-div">URL：{{item.url}}</div>
            <div class="item-div">描述：{{item.desp}}</div>
            <div class="item-div">
              参数
              <ul>
                <li v-for="param in item.params" :key="param.key" style="margin-top: 10px">
                  名称：
                  <el-tag size="small" type="success">{{param.key}}</el-tag>
                  类型：
                  <el-tag size="small" type="success">{{param.type}}</el-tag>
                  说明：
                  <el-tag size="small" type="success">{{param.desp}}</el-tag>
                  是否必须：
                  <el-tag size="small" type="success" v-if="param.must">是</el-tag>
                  <el-tag size="small" type="success" v-else>否</el-tag>
                </li>
              </ul>
            </div>
            <div class="item-div">
              <el-button size="mini" type="warning" @click="useApi(item)">使用</el-button>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </div>
    <div id="section-right">
      <div>
        <h3>API使用</h3>
        <el-form :inline="true" class="form-inline">
          <el-form-item label="URL">
            <el-input v-model="apiForm.url" placeholder="URL"></el-input>
          </el-form-item>
          <el-form-item label="方法">
            <el-select v-model="apiForm.method" placeholder="方法">
              <el-option label="GET" value="GET"></el-option>
              <el-option label="POST" value="POST"></el-option>
              <el-option label="PUT" value="PUT"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="apiSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <el-form label-position="left">
          <el-form-item v-for="item in paramsForm" :key="item.key" :label="item.placeholder" size="mini">
            <el-input :type="item.type" v-model="item.value" :placeholder="item.placeholder"></el-input>
          </el-form-item>
          <el-form-item label="返回结果" prop="desc">
            <el-input type="textarea" v-model="apiForm.result" rows="6"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "api-private",
    data() {
      return {
        activeName: '',
        apiForm: {
          url: '',
          method: '',
          result: ''
        },
        paramsForm: [
          // { key: "secret",
          //   type:'password',
          //   value:"1",
          //   placeholder:"口令"
          // },
          // {key: "publicKey",
          //   type:'text',
          //   value:"fdsfdsfdfdsfsdfsdfsd",
          //   placeholder:"公钥"
          // }
        ],
        items1: [
          {
            method: 'PUT',
            url: '/api/accounts/lcs',
            desp: '受托人为学习社区投票',
            name: '为学习社区投票',
            params: [
              {
                key: 'secret',
                type: 'String',
                desp: '登录口令',
                must: true
              },
              {
                key: 'publicKey',
                type: 'String',
                desp: '受托人公钥',
                must: true
              },
              {
                key: 'lcs',
                type: 'Array',
                desp: '投票目标的PublicKey组成的列表，PublicKey前需要加一个“+”（投票）或者“-”号（撤销投票）',
                must: true
              }
            ]
          }
        ],
        items2: [
          {
            method: 'PUT',
            url: '/api/transactions',
            name: '添加记录',
            desp: '学习社区为用户添加信用记录',
            params: [
              {
                key: 'secret',
                type: 'String',
                desp: '登录口令',
                must: true
              },
              {
                key: 'publicKey',
                type: 'String',
                desp: '学习社区公钥',
                must: true
              },
              {
                key: 'recipientId',
                type: 'String',
                desp: '被添加记录的用户的ID',
                must: true
              },
              {
                key: 'amount',
                type: 'Number',
                desp: '记录值，须为整数',
                must: true
              }
            ]
          }
        ]
      }
    },
    methods: {
      useApi(item) {
        this.apiForm.method = item.method
        this.apiForm.url = item.url
        this.paramsForm = []
        for (let i = 0; i < item.params.length; i++) {
          this.paramsForm.push(
            {
              key: item.params[i].key,
              type: item.params[i].key === 'secret' ? 'password' : 'text',
              value: item.params[i].key === 'secret' ? this.$root.secret : (item.params[i].key === 'publicKey' ? this.$root.accountInfo.publicKey : ''),
              valueType: item.params[i].type,
              placeholder: item.params[i].type === 'Array' ? item.params[i].desp + "(多个值用“,”隔开)" : item.params[i].desp
            })
        }
      },
      apiSubmit() {
        this.$http[this.apiForm.method.toLowerCase()]("http://localhost:7000" + this.apiForm.url.trim(), this.getParams(this.apiForm.method)).then(
          (res) => {
            this.apiForm.result = JSON.stringify(res.body)
          },
          (res) => {
            this.$message.error("网络连接失败")
          }
        )
      },
      getParams(method) {
        let params = {}
        for (let i = 0; i < this.paramsForm.length; i++) {
          if(this.paramsForm[i].value === ''){
            continue
          }
          if (this.paramsForm[i].valueType === 'Array') {
            params[this.paramsForm[i].key] = this.paramsForm[i].value.split(',')
          } else if (this.paramsForm[i].valueType === 'Number') {
            params[this.paramsForm[i].key] = parseInt(this.paramsForm[i].value)
          } else {
            params[this.paramsForm[i].key] = this.paramsForm[i].value
          }
        }
        return method.toLowerCase() === 'get' ? {params: params} : params
      }
    }
  }
</script>

<style scoped>
  .item-div {
    padding: 6px 20px;
  }

  #section-left {
    width: 58%;
    display: inline-block;
  }

  #section-right {
    width: 30%;
    display: inline-block;
    vertical-align: top;
    margin-left: 3%;
    padding-left: 3%;
    border-left: 1px solid #c9c9c9;
    min-height: 700px;
  }
</style>



