<template>
  <div>
    <div>
      <h3>学习社区投票</h3>
      <el-card class="box-card">
        <strong>投票须知</strong>
        <div class="text item">1.只有受托人才能投票</div>
        <div class="text item">2.每位受托人对同一学习社区至多只能投一票</div>
        <div class="text item">3.投票后可以撤销</div>
        <div class="text item">4.每经过一个受托人周期后（约16分钟）刷新得票数，得票超过2/3的候选学习社区成为正式学习社区</div>
      </el-card>
    </div>
    <div  v-if="this.$root.delegateList.indexOf(this.$root.accountInfo.address)>=0">
      <el-table
        ref="multipleTable"
        :data="lcsTableData"
        tooltip-effect="dark"
        style="width: 100%; margin-top: 10px"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          label="选择"
          :selectable="selectable"
          width="55">
        </el-table-column>
        <el-table-column
          width="120"
          type="index">
        </el-table-column>
        <el-table-column
          label="学习社区ID"
          prop="address">
        </el-table-column>
        <el-table-column
          label="学习社区名称"
          prop="username">
        </el-table-column>
        <el-table-column
          label="得票数"
          width="120"
        prop="lc_vote">
        </el-table-column>
        <el-table-column
          label="得票率"
          width="120">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.lc_vote < 68" type="danger">{{(scope.row.lc_vote *100 / 101).toFixed(2) + "%"}}</el-tag>
            <el-tag v-else type="success">{{(scope.row.lc_vote *100 / 101).toFixed(2) + "%"}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        small
        layout="prev, pager, next"
        :total="totalCount"
        @current-change="pageChange"
        style="width: 100%; margin-top: 10px; text-align: center"
      >

      </el-pagination>
      <div style="float: right">
        <el-button size="small" @click="toggleSelection()">取消选择</el-button>
        <el-button size="small" type="success" @click="voteClick()">确认投票</el-button>
      </div>
    </div>
    <div v-else style="margin-top: 15px">
      <el-alert
        title="您不是受托人，无法投票"
        type="error"
        description="请先申请成为受托人，再为学习社区投票"
        show-icon>
      </el-alert>
    </div>
  </div>
</template>

<script>
  export default {
    name: "vote-l-c",
    data() {
      return {
        lcsTableData: [],
        pageSize: 10,
        currentPage: 1,
        totalCount: 0,
        multipleSelection: []
      }
    },
    mounted() {
      this.$root.eventHub.$emit('flush',this.fetchData)
    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      fetchData() {
        this.$http.get("http://localhost:7000/api/lcs", {
          params: {
            limit: this.pageSize,
            offset: (this.currentPage - 1) * this.pageSize,
            orderBy: 'rate:desc',
            random: Math.random() + "."
          }
        }).then(
          (res) => {
            if (res.body.success) {
              this.totalCount = res.body.totalCount
              this.lcsTableData = res.body.lcs
            }
          })
      },
      pageChange(page) {
        this.currentPage = page
        this.fetchData()
      },
      selectable(row, index) {
        return this.$root.accountInfo.lcs.indexOf(row.publicKey) < 0
      },
      voteClick() {
        if(this.multipleSelection.length<=0){
          this.$message.error("请选择候选学习社区")
          return
        }
        this.$confirm('你确定为这' + this.multipleSelection.length + '个候选学习社区投票吗？', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let list = []
          for (let i = 0; i < this.multipleSelection.length; i++) {
            list.push("+" + this.multipleSelection[i].publicKey)
          }
          this.$http.put('http://localhost:7000/api/accounts/lcs', {
            secret: this.$root.secret,
            lcs: list,
            publicKey: this.$root.accountInfo.publicKey
          }).then(
            (res) => {
              if (res.body.success) {
                this.$message.success("投票成功")
              } else {
                this.$message.error("投票失败" + res.body.error)
              }
            },
            (res) => {
              this.$message.error("网络连接失败")
            }
          )
        })
      }
    }
  }
</script>

<style scoped>

</style>



