<template>
  <div>
    <div>
      <h3>学习社区申请</h3>
      <el-alert
        title="学习社区申请要求"
        description="1.您必须是该学习社区的管理员，拥有查询学习社区用户信息的权力
        2.申请后需得到101位受托人超过2/3的选票，才能够成为正式学习社区">
      </el-alert>
    </div>
    <div>
      <h3>申请成为学习社区</h3>
      <el-row style="text-align: center">
        <el-button type="primary" round @click="applyClicked" :disabled="applyed">
          <span v-if="applyed">已申请</span>
          <span v-else>我要申请</span>
        </el-button>
        <p style="color: grey;font-size: small">点击上方按钮即可申请</p>
      </el-row>
    </div>
    <div>
      <h3>申请进度</h3>
      <el-row>
        <p>已获选票数：{{this.lc_vote}}</p>
      </el-row>
      <el-row>
        <el-steps :active="steps" align-center finish-status="success">
          <el-step title="步骤1" description="提交学习社区帐号申请，成为候选学习社区"></el-step>
          <el-step title="步骤2" description="收集受托人选票"></el-step>
          <el-step title="步骤3" description="得票超过2/3（68票），成为学习社区"></el-step>
        </el-steps>
      </el-row>
    </div>

  </div>
</template>

<script>
  import ElRow from "element-ui/packages/row/src/row";

  export default {
    components: {ElRow},
    name: "apply-l-c",
    data() {
      return {
        applyed: false,
        steps: 0,
        username: '',
        lc_vote: 0
      }
    },
    mounted(){
      this.$root.eventHub.$emit('flush', this.init)
    },
    methods: {
      init(){
        if(this.$root.accountInfo.isLC){
          this.applyed =true
          let index = this.$root.lcList.indexOf(this.$root.accountInfo.address)
          this.lc_vote = this.$root.accountInfo.lc_vote

          if(index<0){
            this.steps = 1
          }else {
            this.steps = 2
          }
        }
      },
      applyClicked: function () {
        this.$confirm('此操作不可撤销, 是否继续?', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if (this.$root.accountInfo.username === "") {
            this.$prompt('请输入学习社区名称，请注意该名称将与您的帐号绑定，不可修改', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              inputValidator: (val) => {
                if (val.length < 2 || val.length > 20) {
                  return '名称长度在2-20个字符之间'
                } else {
                  return true
                }
              }
            }).then(({value}) => {
              this.username = value
              this.apply()
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '您取消了申请'
              });
            });
          }else {
            this.apply()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '您取消了申请'
          });
        });

      },
      apply: function () {
        this.$http.put("http://localhost:7000/api/lcs", {
          secret: this.$root.secret,
          username: this.username || this.$root.accountInfo.username
        }).then(
          (res) => {
            if (res.body.success) {
              this.$message.success({
                message: '申请成功!'
              });
              this.applyed = true
              this.steps = 1
            } else {
              this.$message.error({
                message: '申请失败' + res.body.error
              });
            }
          },
          (res)=>{
            this.$message.error('网络连接失败')
          }
        )
      }
    }
  }
</script>

<style scoped>

</style>



